.main-menu {
  width: 60%;
  position: fixed;
  right: 0;
  top: 0;
  height: 100vh;
  z-index: 8;
  background: #fff;
  transition: transform 500ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transform: translateX(calc(100% - 30px));

  &:hover {
    transform: translateX(calc(100% - 60px));

    .menu-trigger {
      transform: translateY(-50%) rotate(-180deg);

    }
  }

  &.open {
    transform: translateX(10%);
    // box-shadow: 0 0 15px 0 rgba(#cecece,0.8);
  }

  ul {
    padding: 20%;

    li {
      display: block;

      a {
        display: block;
        font-size: 2.5rem;
        padding: 1rem 0.5rem;
        background: linear-gradient(90deg, #70d5ff, #0059bc);
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        -webkit-transform: perspective(1px) translateZ(0);
        transform: perspective(1px) translateZ(0);
        position: relative;
        -webkit-transition-property: color;
        transition-property: color;
        -webkit-transition-duration: 0.5s;
        transition-duration: 0.5s;


      &:before {
        content: "";
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: linear-gradient(270deg, #70d5ff, #2676d1);
        -webkit-transform: scaleX(0);
        transform: scaleX(0);
        -webkit-transform-origin: 0 50%;
        transform-origin: 0 50%;
        -webkit-transition-property: transform;
        transition-property: transform;
        -webkit-transition-duration: 0.5s;
        transition-duration: 0.5s;
        -webkit-transition-timing-function: ease-out;
        transition-timing-function: ease-out;
      }
      &:hover, &:focus, &:active {
        color: white;
        background: white;
        -webkit-text-fill-color: white;
      }
      &:hover:before, &:focus:before, &:active:before {
        -webkit-transform: scaleX(1);
        transform: scaleX(1);
        -webkit-transition-timing-function: cubic-bezier(0.52, 1.64, 0.37, 0.66);
        transition-timing-function: cubic-bezier(0.52, 1.64, 0.37, 0.66);
      }
    

        @media screen and (min-width: 362px) {
          padding: 1rem;
        }
      }
    }
  }
}

.menu-trigger {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: -28px;
  cursor: pointer;
  background: none;
  outline: none;
  border: none;
  padding: 10px;
  transform-origin: center center;
  transition: transform 500ms cubic-bezier(0.175, 0.885, 0.32, 1.275);

  span {
    display: block;
    width: 36px;
    margin-top: 6px;
    border-radius: 4px;
    height: 5px;
    background: linear-gradient(342deg, #70d5ff, #0059bc);
  }
}

.social-media {
  width: 30px;
  position: fixed;
  top: 0px;
  left: 0px;
  background: #fff;
  height: 100vh;
  display: flex;
  z-index: 7;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  transition: width 500ms cubic-bezier(0.175, 0.885, 0.32, 1.275);

  &:hover {
    width: 90px;

    a {
      margin-left: 25px;
      margin-top: 2rem;
    }
  }

  a {
    margin-left: 12px;
    background: linear-gradient(135deg, #70d5ff, #0059bc);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    color: #52a2e4;
    padding: 5px 0;
    font-size: 26px;
    display: inline-block;
    border-radius: 50%;
    width: 36px;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: scale(1) translateZ(0);
    transition: all 150ms ease;

    &:hover {
      color: white;
      -webkit-text-fill-color: white;
      transform: scale(1.3) translateZ(0);

      &:before {
        content: "";
        color: #000000;
        -webkit-text-fill-color: black;
        font-family: "Aquatico", sans-serif;
        position: absolute;
        top: -17px;
        font-size: 6px;
        letter-spacing: 1px;
        padding: 7px 7px 4px;
        z-index: -999;
      }

      &:first-child {
        background: radial-gradient(
          circle at 33% 100%,
          #fed373 4%,
          #f15245 30%,
          #d92e7f 62%,
          #9b36b7 85%,
          #515ecf
        );

        &:before {
          content: "Instagram";
        }
      }

      &:nth-child(2) {
        background: #e83e80;

        &:before {
          content: "Dribbble";
        }
      }

      &:nth-child(3) {
        background: linear-gradient(
          to right,
          rgba(0, 99, 220, 1) 0%,
          rgba(0, 99, 220, 1) 50%,
          rgba(255, 0, 132, 1) 51%,
          rgba(255, 0, 132, 1) 100%
        );

        &:before {
          content: "Flickr";
        }
      }

      &:nth-child(4) {
        background: rgb(24, 24, 24);

        &:before {
          content: "Codepen";
        }
      }

      &:last-child {
        background: #552775;

        &:before {
          content: "Github";
        }
      }
    }
  }
}
